import * as React from 'react';
import { Controller, environment } from '@ali/4ever-cangjie';
import { IBasicButton, IToolbarButtonConfig, IconEraser } from '@ali/4ever-component';
import { PluginRoles } from '@ali/4ever-bamboo';
import { clearStyles } from '../../actions';

const MOD = environment.IS_MAC ? 'âŒ˜' : 'Ctrl';

export const CLEAR_STYLE_HOT_KEY = `${MOD}+\\`;

export interface ClearStyleButtonProps extends IToolbarButtonConfig {
  controller: Controller;
  locale?: Record<string, string>;
}

export default function createClearStyleButton(props: ClearStyleButtonProps): IBasicButton {
  const { controller, locale, icon, title, tooltip: tooltipProp, badgeDot, disabled } = props;

  const handleClick = () => {
    controller.run('onAction', clearStyles());
  };

  const tooltip = React.useMemo(() => ({
    title: title || locale?.buttonTitle,
    shortCut: CLEAR_STYLE_HOT_KEY,
    ...tooltipProp,
  }), [title, tooltipProp]);

  const buttonConfig: IBasicButton = ({
    icon: icon || <IconEraser />,
    onClick: handleClick,
    title: title || locale?.buttonTitle,
    tooltip: tooltip,
    badgeDot,
    disabled,
    testid: 'bi-toolbar-clearstyle',
    role: PluginRoles.clear,
  });

  return buttonConfig;

}
